.new-artifacts
	padding-bottom: 60px
	&__title
		padding-bottom: 30px
		font-family: Cormorant
		font-size: 48px
		font-weight: 700
		line-height: 100%
		color: $white
	&__images
		position: relative
		width: 825px
		height: 493px
		background-color: $background_shade
		padding: 16px
		// overflow-x: auto
		// white-space: nowrap
		// -webkit-overflow-scrolling: touch
		.artifacts-img
			width: 100%
		.swipe
			position: absolute
			bottom: 16px
			left: 15px
			display: none
			justify-content: space-between
			width: 224px
			height: 24px
			&__text
				color: rgb(137, 157, 157)
				font-family: Open Sans
				font-size: 16px
				font-weight: 400
				line-height: 150%
			&__arrow
				width: 80px
				color: #899D9D
	&__info
		display: flex
		flex-direction: column
		justify-content: space-between
		width: 255px
		height: 493px
		background-color: $primary_green
		padding: 16px 16px 24px 16px
		.tile-info
			font-family: Cormorant
			font-size: 24px
			font-weight: 700
			line-height: 150%
			letter-spacing: 0%
			color: $background
		.info
			font-family: Open Sans
			font-size: 16px
			font-weight: 400
			line-height: 150%
			letter-spacing: 0%
			color: $green_text

@media (min-width: 992px) and (max-width: 1199.98px)
	.new-artifacts
		&__images
			display: flex
			justify-content: center
			align-items: center
			margin-bottom: 30px
			width: 100%
			height: 555px
		&__info
			width: 100%
			height: 200px

@media (min-width: 768px) and (max-width: 991.98px)
	.new-artifacts
		&__images
			display: flex
			justify-content: center
			align-items: center
			margin-bottom: 30px
			width: 100%
			height: 450px
		&__info
			width: 100%
			height: 250px

@media (min-width: 576px) and (max-width: 767.98px)
	.new-artifacts
		&__images
			display: flex
			align-items: center
			width: 100%
			height: 493px
			margin-bottom: 30px
			overflow-x: scroll
			.artifacts-img
				width: 794px
			.swipe
				display: flex
		&__info
			width: 100%
			height: 350px
@media (min-width: 320px) and (max-width: 575.98px)
	.new-artifacts
		&__images
			display: flex
			align-items: center
			width: 100%
			height: 493px
			margin-bottom: 30px
			overflow-x: scroll
			.artifacts-img
				width: 794px
			.swipe
				display: flex
		&__info
			width: 100%
