.link
	font-family: Open Sans
	font-size: 16px
	font-weight: 700
	line-height: 22px
	letter-spacing: 0%
	&_dark,
	&_green
		&:disabled
			color: $grey
		&:active
			color: $green_active
		&:focus
			border: 1px solid $green_focus
			padding: 5px 10px
		&:hover
			cursor: pointer
			color: $green_hover
			text-decoration: none
			.arrow1-class
				width: 53px
				height: 20px
		.arrow1-class
			width: 43px
			height: 20px
	&_green
		color: $primary_green
	&_dark
		color: $background_shade
