.card
	width: 255px
	height: 493px
	border: none
	outline: none !important
	border: none !important
	output: none !important
	box-shadow: none !important
	&:focus
		outline: none !important
		border: none !important
		output: none !important
		box-shadow: none !important
	&:focus-visible
		outline: none !important
		border: none !important
		output: none !important
		box-shadow: none !important
	.top-sec
		position: relative
		display: flex
		justify-content: center
		align-items: center
		width: 100%
		height: 180px
		background-color: $background_shade
		border: none
		outline: none
		output: none
			outline: none !important
			border: none !important
			output: none !important
			box-shadow: none !important
		.number
			position: absolute
			display: flex
			justify-content: center
			align-items: center
			padding-bottom: 4px
			top: 16px
			left: 16px
			width: 24px
			height: 24px
			border: 1px solid $grey
			border-radius: 100%
			&__item
				text-align: center
				font-family: Cormorant
				font-size: 16px
				font-weight: 700
				line-height: 0
				color: $grey

	.but-sec
		display: flex
		flex-direction: column
		justify-content: space-between
		width: 100%
		height: 313px
		padding-left: 16px
		padding-right: 16px
		padding-top: 16px
		padding-bottom: 24px
		border: 1px solid $background_shade
		background-color: $background
		outline: none
		z-index: 10000
		&__title
			font-family: Cormorant
			font-size: 24px
			font-weight: 700
			line-height: 150%
			letter-spacing: 0%
			color: $white
		&__text
			font-family: Open Sans
			font-size: 16px
			font-weight: 400
			line-height: 150%
			letter-spacing: 0%
			color: $grey
