.swipers
	position: relative
	padding-top: 60px
	padding-bottom: 100px
	.swiper-container

		overflow: hidden
		width: 100%
		height: 555px
		outline: none !important
		border: none !important
		output: none !important
		box-shadow: none !important
		.swiper-wrapper
			width: 100%

			&:focus
				outline: none !important
				border: none !important
				output: none !important
				box-shadow: none !important
			&:focus-visible
				outline: none !important
				border: none !important
				output: none !important
				box-shadow: none !important
			.swiper-slide
				width: 100%
				outline: none !important
				border: none !important
				output: none !important
				box-shadow: none !important
				&:focus
					outline: none !important
					border: none !important
					output: none !important
					box-shadow: none !important
				&:focus-visible
					outline: none !important
					border: none !important
					output: none !important
					box-shadow: none !important
		.gallery-controlls
			display: flex
			justify-content: space-between
			position: absolute
			bottom: 0
			left: 50%
			transform: translateX(-80.5px)
			width: 161px
			height: 50px
			z-index: 10000

			.swiper-button-next,
			.swiper-button-prev
				width: 25px
				height: 50px

			.next-class,
			.prev-class
				width: 25px
				height: 50px
				color: $grey
				&:hover
					color: $primary_green
					width: 35px
				&.swiper-button-disabled
					width: 20px
					color: $grey
			.swiper-pagination
				font-family: Cormorant
				font-size: 24px
				font-weight: 700
				line-height: 150%
				letter-spacing: 0%
				color: $white
				z-index: -1000
@media (max-width: 1199.98px)
	.swiper-slide
		width: 100%
		display: flex
		justify-content: center
@media (max-width: 991.98px)
	.swiper-button-next,
	.swiper-button-prev
		display: none
