.link-research
	position: relative
	font-family: Open Sans
	font-size: 16px
	font-weight: 700
	line-height: 22px
	letter-spacing: 0%
	text-align: left
	text-decoration: none
	color: $primary_green
	&::after
		position: absolute
		content: " "
		top: 6px
		right: -52px
		width: 44px
		height: 14px
		background-image: url('/img/Arrow 1.png')
		background-repeat: no-repeat

	&:hover.link-research::after
		width: 54px
		right: -62px
		background-image: url('/img/ArrowGreen_hover.png')
	&:hover
		color: $green_hover
	&:focus
		padding: 6px 70px 6px 8px
		outline: none
		border: 1px solid $primary_green
	&:focus.link-research::after
		top: 12px
		right: 8px
	&:active.link-research::after
		background-image: url('/img/ArrowActiv.png')
		color: $green_active
	&:disabled
		color: $grey
