.link-slider-right
	position: relative
	&::after
		position: absolute
		content: " "
		top: 6px
		right: -56px
		width: 26px
		height: 14px
		background-image: url('/img/arrowSlider/arrowSliderR.png')
		background-repeat: no-repeat
	&:hover.link-slider-right::after
		width: 36px
		right: -66px
		background-image: url('/img/arrowSlider/arrowSliderRightHover.png')
	&:active.link-slider-right::after
		background-image: url('/img/arrowSlider/arrowSliderRightClick.png')
