.menu
	display: flex
	margin-left: 125px
	margin-top: 0
	margin-bottom: 0
	padding-left: 0

	&__list
		display: flex
		margin-bottom: 0
	&__item
		padding-right: 65px
		list-style-type: none
		.link
			font-family: Open Sans
			font-size: 16px
			font-weight: 400
			line-height: 150%
			letter-spacing: 0%
			text-decoration: none
			color: $white
			&:hover
				color: $primary_green
			&:focus
				padding: 6px
				outline: none
				border: 1px solid #83A440
				color: $white
			&:active
				color: $green_active
				border: none
@media (min-width: 320px) and (max-width: 575.98px)
	.menu
		position: absolute
		top: 65px
		left: 0
		margin-left: 0
		overflow-x: auto // Включение горизонтальной прокрутки
		white-space: nowrap
		max-width: 100% // Ограничиваем ширину меню
		.link
			&:focus
				display: none
		&__list
			margin-left: 0
			padding-left: 0
@media (min-width: 576px) and (max-width: 767.98px)
	.menu
		position: absolute
		top: 65px
		left: 0
		margin-left: 0
		overflow-x: auto // Включение горизонтальной прокрутки
		white-space: nowrap
		max-width: 100% // Ограничиваем ширину меню
		.link
			&:focus
				display: none
		&__list
			margin-left: 0
			padding-left: 0
@media (min-width: 768px) and (max-width: 991.98px)
	.menu
		margin-left: 0
		&__item
			padding-right: 35px
