.help-project
	position: relative
	padding-top: 60px
	padding-bottom: 60px
	background-image: url('../img/help-project-bg.jpeg')
	background-size: cover
	&::before
		position: absolute
		content: ''
		top: 0
		left: 0
		width: 100%
		height: 100%
		background: rgb(29, 47, 48)
		opacity: 0.7
	&__title
		font-family: Cormorant
		font-size: 48px
		font-weight: 700
		line-height: 100%
		font-style: italic
		letter-spacing: 0%
		color: $white
	&__text
		padding-top: 16px
		padding-bottom: 24px

		font-family: Open Sans
		font-size: 16px
		font-weight: 400
		line-height: 150%
		letter-spacing: 0%
		color: $white
	&__form
		display: flex
		flex-direction: column
		justify-content: space-between
		width: 100%
		max-width: 350px
		min-width: 280px
		height: 183px
		.input
			width: 350px
			height: 45px
			padding-left: 16px
			border: none
			font-family: Open Sans
			font-size: 16px
			font-weight: 400
			line-height: 150%
			color: $grey
			&:hover
				background-color: $grey
				color: $white
				cursor: pointer
				border: none
			&:focus
				background-color: $grey
				color: $background_shade
				outline: 1px solid $green_focus

			&:active
				background-color: $white
@media (min-width: 320px) and (max-width: 575.98px)
	.help-project
		background-position: right
		&__text_none
			display: none
		&__title
			padding-bottom: 30px
		.input
			width: 290px
