.hero
	padding-top: 113px
	padding-bottom: 115px
	background-image: url('../img/hero-img.jpeg')
	background-position: center
	background-repeat: no-repeat
	background-size: cover
	&__title
		width: 688px
		font-family: "Cormorant", serif
		font-size: 60px
		font-weight: 700
		line-height: 100%
		letter-spacing: 0%
		color: $white
	&__text
		width: 635px
		font-family: "Open Sans", sans-serif
		font-family: Open Sans
		font-size: 16px
		font-weight: 400
		line-height: 150%
		letter-spacing: 0%
		color: $white

@media (min-width: 768px) and (max-width: 991.98px)
	.hero
		padding-top: 125px
		padding-bottom: 65px
@media (min-width: 576px) and (max-width: 767.98px)
	.hero
		padding-top: 69px
		padding-bottom: 30px
		padding-left: 0
		background-position: center
		background-size: auto
		background-repeat: no-repeat
		&__title
			padding-bottom: 30px
			width: 100%
			max-width: 565px
			min-width: 256px
			font-size: 48px
		&__text
			width: 100%
			max-width: 565px
			min-width: 256px
			font-size: 14px
@media (min-width: 320px) and (max-width: 575.98px)
	.hero
		padding-top: 69px
		padding-bottom: 30px
		padding-left: 17px
		background-position: center
		background-size: auto
		background-repeat: no-repeat
		&__title
			padding-bottom: 30px
			width: 100%
			max-width: 565px
			min-width: 256px
			font-size: 48px
		&__text
			display: none
