.button_green
	border: none
	background-color: $primary-green
	color: $background_shade
	&:hover
		background-color: $green-hover
	&:focus
		outline: none
		box-shadow: 0 0 0 4px $background, 0 0 0 6px $green-focus
	&:active
		background-color: $green_active
	&:disabled
		border: 1px solid $grey
		color: $grey
