.link-slider-left
	position: relative
	margin-left: 50px
	width: 5px
	height: 5px
	&::after
		position: absolute
		display: block
		content: " "
		top: 0
		left: 0
		width: 26px
		height: 14px
		background-image: url('/img/arrowSlider/arrowSliderL.png')
		background-repeat: no-repeat
	&:hover.link-slider-left::after
		width: 36px
		left: -10px
		background-image: url('/img/arrowSlider/arrowSliderLeftHover.png')
	&:active.link-slider-left::after
		left: 0
		background-image: url('/img/arrowSlider/arrowSliderLeftClick.png')
	&:focus.link-slider-left
		padding: 10px
		border: 1px solid $green_active
