.button_transparent
	border: 1px solid $primary-green
	background-color: transparent
	color: $primary-green
	&:hover
		background-color: $primary-green
		color: $background_shade
	&:focus
		outline: none
		box-shadow: 0 0 0 4px $background, 0 0 0 6px $green-focus
	&:active
		background-color: $green_active
	&:disabled
		border: 1px solid $grey
		color: $grey
