.header
	background-color: $background_shade
	&__wrapp
		width: 100%
		height: 60px
		display: flex
		align-items: center

		.logo-class
			width: 65px
			height: 20px
			color: $white
			&:hover
				cursor: pointer
		.box
			margin-left: auto
			width: 110px
			height: 24px
			display: flex
			justify-content: space-between
			.icons-header
				width: 24px
				height: 24px
				color: $primary-green
				cursor: pointer
				&:hover
					color: $green-hover
				&:focus
					outline: none
					border: 1px solid $primary_green

@media (min-width: 768px) and (max-width: 991.98px)
	.header
		&__wrapp
			justify-content: space-between
		.box
			margin-left: 0
@media (min-width: 576px) and (max-width: 767.98px)
	.header
		&__wrapp
			position: relative

@media (min-width: 320px) and (max-width: 575.98px)
	.header
		padding-left: 17px
		&__wrapp
			position: relative
